@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0px;
  padding: 0px;
  font-weight: 300;
  background-color: #a02cff;
  font-family: "Roboto", sans-serif;
}

/* Hide scrollbar for all elements */
::-webkit-scrollbar {
  display: none;
}

/* Optional: Customize scrollbar style */
::-webkit-scrollbar-track {
  background-color: #f5f5f5;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: #000000;
  border-radius: 5px;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.1;
  margin: 0;
}

h1 {
  font-size: clamp(1.875rem, 8vw, 5rem);
}

h2 {
  font-size: clamp(1.40625rem, 6vw, 3.75rem);
}

h3 {
  font-size: clamp(1.171875rem, 5vw, 3.125rem);
}

h4 {
  font-size: clamp(0.703125rem, 3vw, 1.875rem);
}

h5 {
  font-size: clamp(0.5859375rem, 2vw, 1.5625rem);
}

h6 {
  font-size: clamp(0.625rem, 2vw, 1.25rem);
}

p {
  font-size: clamp(0.625rem, 2vw, 1.25rem);
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

div.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
div.main .main-title {
  color: #fff;
  text-align: center;
  margin-top: 20px;
}
div.main .wrapper {
  background-color: #FFF;
  border-radius: 10px;
  box-shadow: 0px 2px 1px 0px #DDD;
  box-sizing: border-box;
  height: 100px;
  width: 80%;
  min-width: 300px;
  max-width: 1000px;
  margin: auto;
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
div.main .input {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #CCC;
  color: #555;
  font-size: 1.3rem;
  width: 90%;
}
div.main .input:focus {
  outline: none;
}
div.main input::-webkit-input-placeholder {
  color: #AAA;
}
div.main .submit {
  margin-top: 30px;
  font-size: 1.6rem;
  padding: 10px;
  border: none;
  width: 40%;
  min-width: 150px;
  max-width: 800px;
  border-radius: 10px;
  background-color: rgb(37, 37, 37);
  color: #fff;
  cursor: pointer;
}
div.main .details {
  display: none;
  margin-bottom: 70px;
}
div.main .details .video-detail {
  background-color: #FFF;
  border-radius: 10px;
  box-shadow: 0px 2px 1px 0px #DDD;
  box-sizing: border-box;
  width: 80%;
  min-width: 300px;
  max-width: 1000px;
  margin: auto;
  margin-top: 50px;
  padding: 20px;
  color: #303030;
  font-weight: 400;
}
div.main .details span {
  color: rgb(0, 133, 0);
  font-weight: 800;
}
div.main .details h4 {
  text-align: center;
  margin-bottom: 10px;
}
div.main .details img {
  width: 50%;
  margin-bottom: 15px;
  border-radius: 5px;
}
div.main .details #thumbnail-container {
  display: flex;
  justify-content: center;
}
div.main .details .comments #user {
  color: rgb(139, 0, 0);
}
div.main .details .comments #comment {
  margin-left: 4%;
  margin-bottom: 15px;
}
div.main .wrong {
  background-color: #FFF;
  border-radius: 10px;
  box-shadow: 0px 2px 1px 0px #DDD;
  box-sizing: border-box;
  width: 80%;
  min-width: 300px;
  max-width: 1000px;
  margin: auto;
  margin-top: 50px;
  padding: 20px;
  color: #ac0000;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 800;
  display: none;
}

.me {
  text-align: center;
  color: #cd99f8;
  margin-top: 15px;
  margin-bottom: 20px;
  display: none;
}

.load-bar {
  width: 50%;
  min-width: 300px;
  max-width: 900px;
  height: 40px;
  border-radius: 30px;
  background: #dcdbd7;
  margin-top: 50px;
  display: none;
}

.load-bar:hover .load-bar-inner,
.load-bar:hover #counter {
  animation-play-state: paused;
}

.load-bar-inner {
  height: 99%;
  width: 0%;
  border-radius: inherit;
  background: rgb(70, 70, 255);
  animation: loader 10s linear infinite;
  color: #fff;
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 15px;
}
.load-bar-inner p {
  padding-left: 20px;
}

@keyframes loader {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
@keyframes counter {
  from {
    left: -25px;
  }
  to {
    left: 323px;
  }
}